.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.bbb {
  padding: 10px;
  margin: 5vh auto 0vw auto;
  font-size: calc(20px + 2vmin);
  background-color: lightgrey;
  border-radius: 25px;
  width: 70vw;
}
#headmy {
  margin-top: 10vh;
}
#headmy img {
  display: block;
  margin: 20px auto 10px auto;
}
#freepik {
  font-size: calc(2px + 2vmin);
}
.bck {
  margin-top: 10vh;
  object-fit: cover;
  width: 99vw;
  height: 100vh;
  position: relative;
  z-index: 0;
}
.bck-overlay {
  position: absolute;
  margin: auto;
  width: 50%;
  z-index: 1
}
.video-my {
  object-fit: cover;
  width: 99vw;
  height: 100vh;
  position: relative;
  z-index: 0;
}
.video-overlay {
  position: absolute;
  margin: auto;
  width: 50%;
  z-index: 1
}
.App-header {
  background-color: darkgrey;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

